CustomFunctions.addFunction(Sin);
CustomFunctions.addFunction(ASin);
CustomFunctions.addFunction(Cos);
CustomFunctions.addFunction(ACos);
CustomFunctions.addFunction(Tan);
CustomFunctions.addFunction(ATan);

