  private static _veryVarFunctions: { [key: string]: Function } = {};

  public static hasVarType(var_type: string): boolean {
    var_type = var_type.toLowerCase();
    if (this._veryVarFunctions[var_type]) {
      return true;
    } else {
      return false;
    }
  }

